        email: " ",
        url: "URL",
        emoji: "emoji",
        uuid: "UUID",
        uuidv4: "UUIDv4",
        uuidv6: "UUIDv6",
        nanoid: "nanoid",
        guid: "GUID",
        cuid: "cuid",
        cuid2: "cuid2",
        ulid: "ULID",
        xid: "XID",
        ksuid: "KSUID",
        datetime: "ISO  ",
        date: "ISO ",
        time: "ISO ",
        duration: "ISO  ",
        ipv4: "IPv4 ",
        ipv6: "IPv6 ",
        cidrv4: "IPv4 ",
        cidrv6: "IPv6 ",
        base64: "base64-encoded ",
