import * as util from "../core/util.js";
const error = () => {
    const Sizable = {
        string: { unit: "", verb: " " },
        file: { unit: "", verb: " " },
        array: { unit: "", verb: " " },
        set: { unit: "", verb: " " },
    };
    function getSizing(origin) {
        return Sizable[origin] ?? null;
    }
    const FormatDictionary = {
        regex: "",
        email: " ",
        url: "",
        emoji: "",
        uuid: "UUID",
        uuidv4: "UUIDv4",
        uuidv6: "UUIDv6",
        nanoid: "nanoid",
        guid: "GUID",
        cuid: "cuid",
        cuid2: "cuid2",
        ulid: "ULID",
        xid: "XID",
        ksuid: "KSUID",
        datetime: "   ISO",
        date: "  ISO",
        time: "  ISO",
        duration: "  ISO",
        ipv4: " IPv4",
        ipv6: " IPv6",
        cidrv4: "   IPv4",
        cidrv6: "   IPv6",
        base64: "  base64-encoded",
        base64url: "  base64url-encoded",
        json_string: "   JSON",
        e164: "   E.164",
        jwt: "JWT",
        template_literal: "",
    };
    const TypeDictionary = {
        nan: "NaN",
    };
    return (issue) => {
        switch (issue.code) {
            case "invalid_type": {
                const expected = TypeDictionary[issue.expected] ?? issue.expected;
                const receivedType = util.parsedType(issue.input);
                const received = TypeDictionary[receivedType] ?? receivedType;
                if (/^[A-Z]/.test(issue.expected)) {
                    return `  :   instanceof ${issue.expected}    ${received}`;
                }
                return `  :   ${expected}    ${received}`;
            }
            case "invalid_value":
                if (issue.values.length === 1)
                    return `  :   ${util.stringifyPrimitive(issue.values[0])}`;
                return `  :     : ${util.joinValues(issue.values, "|")}`;
            case "too_big": {
                const adj = issue.inclusive ? "<=" : "<";
                const sizing = getSizing(issue.origin);
                if (sizing)
                    return `   :    ${issue.origin ?? ""} ${adj} ${issue.maximum.toString()} ${sizing.unit ?? ""}`;
                return `  :    ${issue.origin ?? ""} ${adj} ${issue.maximum.toString()}`;
            }
            case "too_small": {
                const adj = issue.inclusive ? ">=" : ">";
                const sizing = getSizing(issue.origin);
                if (sizing) {
                    return `  :   ${issue.origin}   ${adj} ${issue.minimum.toString()} ${sizing.unit}`;
                }
                return `  :   ${issue.origin}   ${adj} ${issue.minimum.toString()}`;
            }
            case "invalid_format": {
                const _issue = issue;
                if (_issue.format === "starts_with")
                    return `  :     "${issue.prefix}"`;
                if (_issue.format === "ends_with")
                    return `  :     "${_issue.suffix}"`;
                if (_issue.format === "includes")
                    return `  :    "${_issue.includes}"`;
                if (_issue.format === "regex")
                    return `  :     ${_issue.pattern}`;
                return `${FormatDictionary[_issue.format] ?? issue.format}  `;
            }
            case "not_multiple_of":
                return `  :      ${issue.divisor}`;
            case "unrecognized_keys":
                return `${issue.keys.length > 1 ? "" : ""} ${issue.keys.length > 1 ? "" : ""}: ${util.joinValues(issue.keys, " ")}`;
            case "invalid_key":
                return `    ${issue.origin}`;
            case "invalid_union":
                return "  ";
            case "invalid_element":
                return `    ${issue.origin}`;
            default:
                return "  ";
        }
    };
};
export default function () {
    return {
        localeError: error(),
    };
}
