        url: "  ",
        emoji: "",
        uuid: "   ",
        uuidv4: "     4",
        uuidv6: "     6",
        nanoid: "  ",
        guid: "   ",
        cuid: "   ",
        cuid2: "    2",
        ulid: "   ",
        xid: "  ",
        ksuid: "    ",
        datetime: "    ",
        date: "   ",
        time: "   ",
        duration: "   ",
